'use client';

import React from 'react';
import { TrendingUp, TrendingDown, Minus } from 'lucide-react';
import { NEWS_ITEMS } from '@/lib/data';
import type { NewsItem } from '@/lib/types';

const SENTIMENT_CONFIG = {
  positive: {
    icon: TrendingUp,
    color: '#10b981',
    bg: 'rgba(16,185,129,0.06)',
    border: 'rgba(16,185,129,0.15)',
  },
  negative: {
    icon: TrendingDown,
    color: '#ef4444',
    bg: 'rgba(239,68,68,0.06)',
    border: 'rgba(239,68,68,0.15)',
  },
  neutral: {
    icon: Minus,
    color: 'rgba(255,255,255,0.4)',
    bg: 'rgba(255,255,255,0.02)',
    border: 'rgba(255,255,255,0.07)',
  },
};

function timeAgo(iso: string): string {
  const secs = Math.floor((Date.now() - new Date(iso).getTime()) / 1000);
  if (secs < 60)   return `${secs}s ago`;
  if (secs < 3600) return `${Math.floor(secs / 60)}m ago`;
  if (secs < 86400) return `${Math.floor(secs / 3600)}h ago`;
  return `${Math.floor(secs / 86400)}d ago`;
}

function NewsCard({ item }: { item: NewsItem }) {
  const cfg = SENTIMENT_CONFIG[item.sentiment];
  const Icon = cfg.icon;
  return (
    <a
      href={item.url}
      style={{
        display: 'block',
        padding: '16px',
        borderRadius: '10px',
        background: cfg.bg,
        border: `1px solid ${cfg.border}`,
        textDecoration: 'none',
        transition: 'border-color 0.2s',
        cursor: 'pointer',
      }}
    >
      <div style={{ display: 'flex', gap: '12px', alignItems: 'flex-start' }}>
        <div
          style={{
            width: '28px',
            height: '28px',
            borderRadius: '6px',
            background: `${cfg.color}18`,
            border: `1px solid ${cfg.color}30`,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            flexShrink: 0,
            marginTop: '2px',
          }}
        >
          <Icon size={13} color={cfg.color} />
        </div>
        <div style={{ flex: 1, minWidth: 0 }}>
          <div style={{ display: 'flex', justifyContent: 'space-between', gap: '8px', marginBottom: '6px', flexWrap: 'wrap' }}>
            <span style={{ fontSize: '11px', fontWeight: 600, color: '#d4375a', textTransform: 'uppercase', letterSpacing: '0.08em' }}>
              {item.source}
            </span>
            <span style={{ fontSize: '10px', color: 'rgba(255,255,255,0.3)' }}>{timeAgo(item.publishedAt)}</span>
          </div>
          <div style={{ fontSize: '13px', fontWeight: 600, color: '#e8eaf0', lineHeight: 1.4, marginBottom: '6px' }}>
            {item.title}
          </div>
          <div style={{ fontSize: '11px', color: 'rgba(255,255,255,0.5)', lineHeight: 1.5, marginBottom: '8px' }}>
            {item.summary}
          </div>
          {item.tickers && item.tickers.length > 0 && (
            <div style={{ display: 'flex', gap: '6px', flexWrap: 'wrap' }}>
              {item.tickers.map(t => (
                <span
                  key={t}
                  style={{
                    padding: '2px 7px',
                    borderRadius: '4px',
                    background: 'rgba(139,0,48,0.12)',
                    border: '1px solid rgba(139,0,48,0.2)',
                    fontSize: '10px',
                    fontWeight: 700,
                    color: '#d4375a',
                    fontFamily: 'monospace',
                    letterSpacing: '0.05em',
                  }}
                >
                  {t}
                </span>
              ))}
            </div>
          )}
        </div>
      </div>
    </a>
  );
}

interface NewsFeedProps {
  limit?: number;
  items?: NewsItem[];
}

export default function NewsFeed({ limit, items }: NewsFeedProps) {
  const feed = (items ?? NEWS_ITEMS).slice(0, limit ?? undefined);
  return (
    <div className="glass" style={{ borderRadius: '16px', padding: '24px' }}>
      <div className="label-text" style={{ marginBottom: '4px' }}>Market Intelligence</div>
      <div style={{ fontSize: '18px', fontWeight: 700, color: '#e8eaf0', marginBottom: '20px' }}>News Feed</div>
      <div style={{ display: 'flex', flexDirection: 'column', gap: '10px' }}>
        {feed.map(item => (
          <NewsCard key={item.id} item={item} />
        ))}
      </div>
    </div>
  );
}
