'use client';

import React, { useMemo } from 'react';
import { TICKER_TAPE } from '@/lib/data';

export default function TickerTape() {
  const items = useMemo(() => [...TICKER_TAPE, ...TICKER_TAPE], []);

  return (
    <div
      style={{
        position: 'fixed',
        top: '64px',
        left: 0,
        right: 0,
        height: '36px',
        zIndex: 40,
        background: 'rgba(4,5,8,0.95)',
        borderBottom: '1px solid rgba(255,255,255,0.05)',
        overflow: 'hidden',
        display: 'flex',
        alignItems: 'center',
      }}
    >
      <div className="ticker-track">
        {items.map((item, i) => (
          <div
            key={i}
            style={{
              display: 'flex',
              alignItems: 'center',
              gap: '8px',
              padding: '0 24px',
              whiteSpace: 'nowrap',
              borderRight: '1px solid rgba(255,255,255,0.05)',
            }}
          >
            <span
              style={{
                fontSize: '11px',
                fontWeight: 700,
                color: '#e8eaf0',
                letterSpacing: '0.08em',
                fontFamily: 'monospace',
              }}
            >
              {item.ticker}
            </span>
            <span style={{ fontSize: '11px', color: 'rgba(255,255,255,0.5)', fontFamily: 'monospace' }}>
              {item.price.toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 2 })}
            </span>
            <span
              style={{
                fontSize: '10px',
                fontWeight: 600,
                color: item.change >= 0 ? '#10b981' : '#ef4444',
                fontFamily: 'monospace',
              }}
            >
              {item.change >= 0 ? '+' : ''}{item.changePct.toFixed(2)}%
            </span>
          </div>
        ))}
      </div>
    </div>
  );
}
