'use client';

import React, { useState } from 'react';
import Background from '@/components/Background';
import Navbar from '@/components/Navbar';
import TickerTape from '@/components/TickerTape';
import NewsFeed from '@/components/NewsFeed';
import { NEWS_ITEMS } from '@/lib/data';

const CATEGORIES = [
  { label: 'All', tickers: null },
  { label: 'Equities', tickers: ['AAPL', 'MSFT', 'NVDA', 'GOOGL', 'JPM', 'TSLA', 'META', 'AMZN'] },
  { label: 'Commodities', tickers: ['GC=F', 'SI=F', 'CL=F'] },
  { label: 'Macro', tickers: ['JPM', 'SPY', 'GC=F'] },
  { label: 'AI & Tech', tickers: ['NVDA', 'MSFT', 'GOOGL', 'META', 'ARKK'] },
];

export default function NewsPage() {
  const [activeCategory, setActiveCategory] = useState('All');

  const category = CATEGORIES.find(c => c.label === activeCategory) ?? CATEGORIES[0];
  const filtered = category.tickers
    ? NEWS_ITEMS.filter(n => n.tickers?.some(t => category.tickers!.includes(t)))
    : NEWS_ITEMS;

  return (
    <>
      <Background />
      <Navbar />
      <TickerTape />
      <main style={{ position: 'relative', zIndex: 1, maxWidth: '1400px', margin: '0 auto', padding: '120px 24px 64px' }}>
        <div style={{ marginBottom: '32px' }}>
          <div className="label-text" style={{ marginBottom: '8px' }}>Market Intelligence</div>
          <h1 style={{ fontSize: '32px', fontWeight: 800, color: '#e8eaf0' }}>News Feed</h1>
        </div>

        {/* Category filter */}
        <div style={{ display: 'flex', gap: '8px', flexWrap: 'wrap', marginBottom: '24px' }}>
          {CATEGORIES.map(c => (
            <button
              key={c.label}
              onClick={() => setActiveCategory(c.label)}
              style={{
                padding: '7px 16px',
                borderRadius: '8px',
                fontSize: '12px',
                fontWeight: 600,
                border: 'none',
                cursor: 'pointer',
                background: activeCategory === c.label ? 'rgba(139,0,48,0.3)' : 'rgba(255,255,255,0.04)',
                color: activeCategory === c.label ? '#e06f88' : 'rgba(255,255,255,0.5)',
                outline: activeCategory === c.label ? '1px solid rgba(139,0,48,0.4)' : '1px solid rgba(255,255,255,0.07)',
                transition: 'all 0.15s',
                letterSpacing: '0.04em',
              }}
            >
              {c.label}
            </button>
          ))}
        </div>

        <NewsFeed items={filtered} />
      </main>
    </>
  );
}
